.timeline-item.has-not-image .timeline-item-title {
  position: relative;
}
.timeline-item.has-not-image .timeline-item-title:after {
  content: "";
  width: 2rem;
  height: 2rem;
  border-radius: 2rem;
  background-color: #cdcdcd;
  position: absolute;
  left: -3rem;
  top: 0;
}
.timeline-item .timeline-item-image-wrapper {
  position: relative;
}
.timeline-item .timeline-item-image-wrapper:before {
  content: "";
  position: absolute;
  left: -2rem;
  top: 1rem;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
  z-index: -1;
}
